import { Component } from '@angular/core';
import { SimpleSmoothScrollService, ISimpleSmoothScrollOption } from 'ng2-simple-smooth-scroll';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'raravis-spa';
  
  constructor(private smooth: SimpleSmoothScrollService) { }

  ngOnInit() {
    this.smooth.smoothScrollToAnchor();
  }

  goTop() {
    let opt : ISimpleSmoothScrollOption = { duration: 500, easing: 'linear' };
    this.smooth.smoothScrollToTop(opt);
  }

}
